import React from "react";

const Order = ({ items, order }) => {
  const renderItem = key => {
    const name = items[key].name;
    const count = order[key];
    const points = count * items[key].karmaPoints;
    return (
      <li key={key}>
        {name} * {count} = {points} points
      </li>
    );
  };

  const countTotal = () => {
    const total = Object.keys(order).reduce((sum, cur) => {
      sum += order[cur] * items[cur].karmaPoints;
      return sum;
    }, 0);
    return total;
  };

  const displayTotal = () => {
    return countTotal() ? (
      <h3>Total: +{countTotal()} karma points to your karma</h3>
    ) : (
      <h3>You have not accepted any challenges yet</h3>
    );
  };

  return (
    <div className="order">
      <h2>Your order</h2>
      <ul>{Object.keys(order).map(key => renderItem(key))}</ul>
      {displayTotal()}
    </div>
  );
};

export default Order;
