import React from "react";

const Item = ({ item, id, addToOrder }) => {
  const { name, image, desc, karmaPoints } = item;
  return (
    <div className="deed">
      <img src={image} alt={name}></img>
      <button onClick={() => addToOrder(id)}>Accept the challenge</button>
      <h3>{name}</h3>
      <p>{desc}</p>
      <h4>+ {karmaPoints} to your karma</h4>
    </div>
  );
};

export default Item;
