import React from "react";
import Item from "./item";

const Menu = ({ items, addToOrder }) => {
  return (
    <div>
      <ul className="items">
        {Object.keys(items).map(key => {
          return (
            <Item
              key={key}
              item={items[key]}
              id={key}
              addToOrder={addToOrder}
            />
          );
        })}
      </ul>
    </div>
  );
};

export default Menu;
